import {Component, OnInit} from '@angular/core';
import {Usuario} from "../../../content/models/usuario";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {AuthService} from "../../../core/authentication/auth.service";
import {UsuarioService} from "../../../content/services/usuario.service";
import {MessageService} from "../../../core/services/message.service";
import {PerfilUsuarioEnum} from "../../../content/models/perfil-usuario.enum";

@Component({
  selector: 'app-info-usuario',
  templateUrl: './info-usuario.component.html',
  styleUrls: ['./info-usuario.component.scss']
})
export class InfoUsuarioComponent implements OnInit {

  usuario: Usuario;
  form: FormGroup
  usuarios: Usuario[];

  constructor(private authService: AuthService,
              private formBuilder: FormBuilder,
              private usuarioService: UsuarioService,
              private messageService: MessageService) {
    this.usuarioService.list().subscribe((users: Usuario[]) => {
      this.usuarios = users;
    });
  }

  ngOnInit() {
    this.usuario = this.authService.getauthData();
    this.form = this.formBuilder.group({
      nome: [this.usuario.nome, Validators.compose([Validators.maxLength(50), Validators.required])],
      email: [{value: this.usuario.email, disabled: true}],
      senha: [this.usuario.senha, Validators.compose([Validators.minLength(8), Validators.required])],
      perfil: [{
        value: this.usuario.perfil,
        disabled: this.usuario.perfil.nome === PerfilUsuarioEnum.VISITANTE
      }, Validators.required],
      ativo: [this.usuario.ativo, Validators.required],
      telefone: [this.usuario.telefone, Validators.compose([Validators.maxLength(16),
        Validators.minLength(10)])],
      cep: [this.usuario.cep, Validators.compose([Validators.minLength(8),
        Validators.maxLength(8)])],
      endereco: [this.usuario.endereco, Validators.compose([Validators.maxLength(50)])],
    });

  }

  salvar() {
    this.usuarioService.list().subscribe((usuarios: Usuario[]) => {
      const users: Usuario[] = usuarios.filter(user => user.perfil.nome === PerfilUsuarioEnum.ADMINISTRADOR && user.ativo === true);
      if (users.length <= 1 &&
        this.usuario.perfil.nome === PerfilUsuarioEnum.ADMINISTRADOR && (this.form.value.perfil === PerfilUsuarioEnum.VISITANTE
          || this.form.value.ativo === false)) {
        this.messageService.showMessageInfo('Impossvel salvar as alteraes, pois deve haver ao menos um administrador.');
      } else {
        const user: Usuario = this.getInformacoes(this.form.value);
        this.usuarioService.update(user.email, user).subscribe(data => {
          this.authService.updateInfo(user);
          this.messageService.showMessageSucessfull('Dados alterados com sucesso!');
        }, error => {
          this.messageService.showMessageError('Erro ao atualizar as informaes...');
        });
      }
    });
  }

  getInformacoes(formValues) {
    return {
      perfil: formValues.perfil === undefined ? this.usuario.perfil : formValues.perfil,
      email: this.usuario.email,
      nome: formValues.nome,
      ativo: formValues.ativo,
      senha: formValues.senha,
      cep: formValues.cep,
      endereco: formValues.endereco,
      telefone: formValues.telefone
    };
  }
}
